.spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 2;
  perspective: 200px;
}

i {
  display: block;
  position: absolute;
  opacity: 1;
}
i b {
  display: block;
  width: 6px;
  height: 6px;
  border-radius: 6px;
  background: white;
  box-shadow: 0 0 14px white;
  animation-name: spin;
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}

i:nth-child(1) {
  transform: rotate(3.6deg) translate3d(140px, 0, 0);
}
i:nth-child(1) b {
  animation-delay: 0.01531s;
}

